import React from 'react';

export default class Start extends React.Component {

  render() {
    return (
      <section className={"Start" + (this.props.afficheGame ? " close" : "")}>
          <div>

              <form onSubmit={this.props.submitValue}>
                <label>
                  <input type="text" value={this.props.value} onChange={this.props.recupValue} placeholder="Pseudo" autoFocus required/>
                </label>
                <div>
                    <div>
                        <label>
                          <input type="radio" name="radio" onChange={() => this.props.difficultyFunc('difficultySimple')} value="" required/>
                          Facile
                        </label>
                    </div>
                    <div>
                        <label>
                          <input type="radio" name="radio" onChange={() => this.props.difficultyFunc('difficultyNormal')} value="" required/>
                          Normal
                        </label>
                    </div>
                    <div>
                        <label>
                          <input type="radio" name="radio" onChange={() => this.props.difficultyFunc('difficultyDifficile')} value="" required/>
                          Difficile
                        </label>
                    </div>
                </div>
                <input type="submit" value="Envoyer" />
              </form>


          </div>
      </section>
    );
  }
}
